/*generated with Input range slider CSS style generator (version 20211225)
https://toughengineer.github.io/demo/slider-styler*/
input[type=range].styled-slider {
  width: 100%;
  height: 2.2em;
  -webkit-appearance: none;
}

/*progress support*/
input[type=range].styled-slider.slider-progress {
  --range: calc(var(--max) - var(--min));
  --ratio: calc((var(--value) - var(--min)) / var(--range));
  --sx: calc(0.5 * 18px + var(--ratio) * (100% - 18px));
  background: transparent;
  cursor: pointer;
}

input[type=range].styled-slider:focus {
  outline: none;
}

/*webkit*/
input[type=range].styled-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 18px;
  height: 10px;
  border-radius: 5px;
  background: linear-gradient(180deg, #3B3A40 0%, #1C1C21 100%);
  border: 1px solid #1D1B24;
  box-shadow: inset 1px 0 3px 0 rgba(0,0,0,0.50);
  margin-top: calc(max((18px - 6px - 6px) * 0.5,0px) - max(10px * 0.5,1px));
}

input[type=range].styled-slider::-webkit-slider-runnable-track {
  height: 18px;
  border: 6px solid #1D1B24;
  border-radius: 12px;
  background: #15121B;
  box-shadow: linear-gradient(180deg, #1D1B24 0%, #1C1C21 100%);
}

input[type=range].styled-slider.slider-progress::-webkit-slider-runnable-track {
  background: linear-gradient(#B2B464,#B2B464) 0/var(--sx) 100% no-repeat, #15121B;
}

input[type=range].styled-slider.slider-progress:hover::-webkit-slider-runnable-track {
  background: linear-gradient(#0061c3,#0061c3) 0/var(--sx) 100% no-repeat, #15121B;
}

input[type=range].styled-slider.slider-progress:active::-webkit-slider-runnable-track {
  background: linear-gradient(#2f98f9,#2f98f9) 0/var(--sx) 100% no-repeat, #15121B;
}

/*mozilla*/
input[type=range].styled-slider::-moz-range-thumb {
  width: max(16px, 0px); //calc(18px - 1px - 1px) = 16px
  height: max(8px, 0px); //calc(10px - 1px - 1px) = 8px
  border-radius: 5px;
  background: linear-gradient(180deg, #3B3A40 0%, #1C1C21 100%);
  border: 1px solid #1D1B24;
  box-shadow: inset 1px 0 3px 0 rgba(0,0,0,0.50);
}

input[type=range].styled-slider::-moz-range-track {
  height: max(4px, 0px); //calc(18px - 6px - 6px) = 4px
  border: 6px solid #1D1B24;
  border-radius: 12px;
  background: #15121B;
  box-shadow: linear-gradient(180deg, #1D1B24 0%, #1C1C21 100%);
}

input[type=range].styled-slider.slider-progress::-moz-range-track {
  background: linear-gradient(#B2B464,#B2B464) 0/var(--sx) 100% no-repeat, #15121B;
}

input[type=range].styled-slider.slider-progress:hover::-moz-range-track {
  background: linear-gradient(#0061c3,#0061c3) 0/var(--sx) 100% no-repeat, #15121B;
}

input[type=range].styled-slider.slider-progress:active::-moz-range-track {
  background: linear-gradient(#2f98f9,#2f98f9) 0/var(--sx) 100% no-repeat, #15121B;
}

/*ms*/
input[type=range].styled-slider::-ms-fill-upper {
  background: transparent;
  border-color: transparent;
}

input[type=range].styled-slider::-ms-fill-lower {
  background: transparent;
  border-color: transparent;
}

input[type=range].styled-slider::-ms-thumb {
  width: 18px;
  height: 10px;
  border-radius: 5px;
  background: linear-gradient(180deg, #3B3A40 0%, #1C1C21 100%);
  border: 1px solid #1D1B24;
  box-shadow: inset 1px 0 3px 0 rgba(0,0,0,0.50);
  margin-top: 0;
  box-sizing: border-box;
}

input[type=range].styled-slider::-ms-track {
  height: 18px;
  border-radius: 12px;
  background: #15121B;
  border: 6px solid #1D1B24;
  box-shadow: linear-gradient(180deg, #1D1B24 0%, #1C1C21 100%);
  box-sizing: border-box;
}

input[type=range].styled-slider.slider-progress::-ms-fill-lower {
  height: max(4px, 0px); //calc(18px - 6px - 6px) = 4px
  border-radius: 12px 0 0 12px;
  margin: -6px 0 -6px -6px;
  background: #B2B464;
  border: 6px solid #1D1B24;
  border-right-width: 0;
}

input[type=range].styled-slider.slider-progress:hover::-ms-fill-lower {
  background: #0061c3;
}

input[type=range].styled-slider.slider-progress:active::-ms-fill-lower {
  background: #2f98f9;
}



@media only screen and (max-width: 712px) {
  #root {
    top: 20px;
  }
  .v-line {
    display: none;
  }
}
@media only screen and (max-width: 695px) {
  .h-line.hidden {
    display: block;
  }
}

@media only screen and (max-width: 376px) {
  body {
    width: 100%;
    margin: 0;
  }
  #root {
    top: 0px;
    margin-bottom: 110px;
  }
  .pad-container {
    margin-bottom: 36px;
  }
  .drum-pad {
    zoom: 0.75;
  }
}